html,
body,
ul,
li,
button,
input,
textarea,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  border: 0;
  font-family: "Ubuntu", sans-serif;
  font-size: 16px;
  line-height: inherit;

  scroll-behavior: smooth;
}

body {
  line-height: 1.5;
}

strong {
  font-weight: 500;
}

a {
  color: $highlight-color;
  text-decoration: none;
}
